$(document).ready(function () {
	if ($('.FBI_page').length > 0) {

		$('.office-items select').change(function () {
			console.log(themeVars.ajaxurl);
			$.ajax({
				url: themeVars.ajaxurl,
				dataType: 'json',
				method: "GET",
				data: {
					action: 'get_fbi_data',
				},
				success: function(data){   /*       .  */
					console.log(data);            /*   data    index.php. */
				},
			})
		})
	}
})



